.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  font-family: "Lato", sans-serif;
}

.sidenav {
  height: 100%;
  width: 180px;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #e6e600;
  overflow-x: hidden;
  padding-top: 20px;
}

.sidenav a {
  padding: 6px 8px 6px 16px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
}

.sidenav a:hover {
  color: #f1f1f1;
}

.main {
  margin-left: 180px; /* Same as the width of the sidenav */
  padding: 0px 10px;
}

@media screen and (max-height: 450px) {
  .sidenav {padding-top: 15px;}
  .sidenav a {font-size: 18px;}
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.topnav {
  overflow: hidden;
  background-color: #008ae6;
  margin-left: 180px;
}

.topnav a {
  
  float: left;
  color: #080808;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 27px;

}

.topnav a:hover {
  background-color: #ddd;
  color: black;
}

.batch-input-position{
  float: right;

}
.scrollit {
  overflow:scroll;
  height:770px;
}

.sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 9998;
  background-color: gray;

}

.sticky-col {
  position: -webkit-sticky;
  position: sticky;
  background-color: white;
  z-index: 0;
}

.first-col {
  left: 0px;
  z-index: 4;
}
.first-header {
  z-index: 5;
  left: 0px;
}
.second-col {
  left: 100px;
  z-index: 4;
}
.second-header {
  z-index: 5;
  left: 100px;
}
.third-col {
  left: 300px;
  z-index: 4;
}
.third-header {
  z-index: 5;
  left: 300px;
}

.sticky-header {
  position: -webkit-sticky;
  position: sticky;
  
}

.sticky-header-position{
  z-index: 5;
  top: 0px;
}

.row-height-cell-table{
  line-height: 10px;
  height: 35px;
}

.row-height-header-table{
  line-height: 10px;
  height: 30px;
}

.row-height-input-table{
  line-height: 10px;
  height: 20px;
}